import '@telerik/kendo-theme-default/styles/packages/dropdowns';
import { Component, Renderer, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Keys } from './common/keys';

/**
 * @hidden
 */
@Component({
    selector: 'kendo-searchbar',
    template: `
        <input #input
            [value]="value"
            [disabled]="disabled"
            [placeholder]="placeholder"
            [class]="'k-input'"
            (input)="handleInput($event.target.value)"
            (focus)="handleFocus($event)"
            (blur)="handleBlur($event)"
            (keydown)="handleKeyDown($event)"
        />
   `
})

export class SearchBarComponent {
    @Input() public disabled: boolean;

    @Input() public placeholder: string;

    @Input()
    get value(): any {
        return this._value;
    };

    set value(value: any) {
        this._value = value || "";
    }

    @Output() public onFilter: EventEmitter<any> = new EventEmitter();
    @Output() public onBlur: EventEmitter<any> = new EventEmitter();
    @Output() public onFocus: EventEmitter<any> = new EventEmitter();
    @Output() public onClick: EventEmitter<any> = new EventEmitter();
    @Output() public onNavigate: EventEmitter<any> = new EventEmitter();

    @ViewChild("input") public input: ElementRef;

    public hasSelection: boolean;
    public _value: string = "";

    private renderer: Renderer;
    private selectionChangeListener: any;

    constructor(renderer: Renderer) {
        this.renderer = renderer;
    }
    public handleKeyDown(event: any): void {
        if (event.keyCode === Keys.up ||
            event.keyCode === Keys.down ||
            event.keyCode === Keys.enter ||
            event.keyCode === Keys.esc) {
            event.preventDefault();
            this.onNavigate.emit(event);
        }
    }

    public handleInput(text: string): void {
        this.onFilter.emit(text);
    }

    public handleFocus(event: any): void {
        this.onFocus.emit(event);
        this.selectionChangeListener = this.renderer.listenGlobal('document', 'selectionchange', this.onSelectionChange.bind(this));
    }

    public handleBlur(event: any): void {
        this.onBlur.emit(event);
        this.selectionChangeListener();
    }

    public onSelectionChange(event: any): void {
        this.hasSelection = this.input.nativeElement.selectionStart !== this.input.nativeElement.selectionEnd;
    }

    public focus(): void {
        this.renderer.invokeElementMethod(this.input.nativeElement, 'focus');
    }
}



